.s-hero{
    margin-top: 11.9rem;
    background-color: $purple;
    padding-top: 3.9rem;
    .container{
        @include flex(row, flex-start, space-between)
    }
    .text{
        width: 100%;
        max-width: 55.5rem;
        h1{
            font-family: 'PT Sans', sans-serif; 
            font-weight: 700;
            font-size: 4.8rem;
            line-height: 120%;
            color: $white;
            margin-bottom: 1.6rem;
        }
        p{
            font-family: 'Lato';
            font-weight: 400;
            font-size: 1.8rem;
            line-height: 150%;
            color: $white;
            max-width: 48.8rem ;
            margin-bottom: 3.2rem;
        }
        button{
            @include style-btn(5.4rem, $yellow, #7b4ad9);
            font-family: 'PT Sans', sans-serif;
            font-size: 1.7rem;
            margin-bottom: 8.6rem;
        }
         
        .conheca{
            display: flex;
            align-items: center;
            gap: 16px;
            width: 100%;
            max-width: 21.5rem;
            .icon{
                padding: 9px;
                background: rgba(124, 74, 217, 0.4);
                border-radius: 0.6rem;
                width: 4.2rem;
                height: 4.2rem;
            }
            span{
                font-family: 'Lato', sans-serif;
                font-weight: 700;
                font-size: 1.6rem;
                line-height: 1.9rem;
                color: $white;

            }
        }
        
    }
    .image{
        position: relative;
        top: 3.7rem;
    }
    @include resposive(991px){
        .container{
            flex-direction: column;
        }
        .text{
            max-width: 100%;
            h1{
                text-align: center;
            }
            p{
                max-width: 100%;
                text-align: center;
            }
            button{
                margin: 0 auto;
                display: block;
                margin-bottom: 2rem;
                margin-bottom: 4.0rem;
            }
            .conheca{
                justify-content: center;
                max-width: 100%;
            }

        }
    }
    @include resposive(600px){
        padding-top: 2rem;
        margin-top: 8rem;
        .text{
            h1{
                font-size: 4.3rem;
            }
        }
        .image{
            left: -21px;
        }
    }
}